<template>
  <div>
    <!---->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!---->
    <el-card>
      <!---->
      <el-row :gutter="20">
        <el-col :span="8">
          <el-input
            placeholder=""
            v-model="queryInfo.key"
            clearable
            @clear="getPermissionList"
          >
            <el-button
              @click="getPermissionList"
              slot="append"
              icon="el-icon-search"
            ></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="showAddDialog"></el-button>
        </el-col>
      </el-row>
      <!---->
      <el-table
        :data="permissionlist"
        border
        stripe
        row-key="id"
        :tree-props="{ children: 'children', hasChildren: 'hasChildren' }"
      >
      <el-table-column type="index"></el-table-column>
      <el-table-column prop="name" label=""></el-table-column>
      <el-table-column prop="pCreateBy" label=""></el-table-column>
      <el-table-column prop="pModifyBy" label=""></el-table-column>
      <el-table-column prop="pName" label=""></el-table-column>
      <el-table-column prop="pName" label=""></el-table-column>
      <el-table-column label="" width="180">
        <template slot-scope="scope">
            <el-button
              type="primary"
              size="mini"
              icon="el-icon-edit"
              @click="showEditDialog(scope.row.pID)"
              ></el-button
            >
            <el-button
              type="danger"
              size="mini"
              icon="el-icon-delete"
              @click="removeMenuById(scope.row.pID)"
              ></el-button
            >
          </template>
      </el-table-column>
      </el-table>
    </el-card>
  </div>
</template>
<script>
export default {
  data () {
    return {
      // 
      queryInfo: {
        key: ''
      },
      // 
      permissionlist: []
    }
  },
  created () {
    // 
    this.getPermissionList()
  },
  methods: {
    // 
    showAddDialog () {},
    // 
    async getPermissionList () {
      const { data: res } = await this.$http.get('permissions/trees', {
        params: { key: this.queryInfo.key }
      })
      if (!res.success) return this.$message.error(res.msg)
      this.permissionlist = res.response.data
    }
  }
}
</script>
<style lang="less" scoped>
</style>
