<template>
  <div>
    <!---->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!---->
    <el-card class="box-card">
      <!---->
      <el-row :gutter="20">
        <el-col :span="8">
          <el-input
            placeholder=""
            v-model="queryInfo.key"
            clearable
            @clear="getUserList"
          >
            <el-button
              @click="getUserList"
              slot="append"
              icon="el-icon-search"
            ></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="addDialogVisible = true"
            ></el-button
          >
        </el-col>
      </el-row>
      <!---->
      <el-table   :data="userlist" border stripe>
        <el-table-column type="index"></el-table-column>
        <el-table-column label="" prop="uRealName"></el-table-column>
        <el-table-column label="" prop="uLoginName"></el-table-column>
        <el-table-column label="" prop="uPhone"></el-table-column>
        <el-table-column label="" prop="uEmail"></el-table-column>
        <el-table-column label="" prop="uAddress"></el-table-column>
        <el-table-column label="" width="180">
           <template slot-scope="scope">
                    <el-tag v-for="item in scope.row.rNames" :key="item.Id" >{{item}}</el-tag>
          </template>
        </el-table-column>
        <el-table-column
          label=""
          prop="uSex"
          :formatter="formatterSex"
          width="50"
        ></el-table-column>
        <el-table-column
          label=""
          prop="uCreateTime"
          :formatter="formatDateTime"
        ></el-table-column>
        <el-table-column
          label=""
          prop="uLastErrTime"
          :formatter="formatDateTime"
        ></el-table-column>
        <!-- <el-table-column label="" width="70">
          <template slot-scope="scope">
            <el-switch
              v-model="scope.row.uIsDelete"
              @change="userStateChanged(scope.row)"
            ></el-switch>
          </template>
        </el-table-column> -->
        <el-table-column label="" width="120">
          <template slot-scope="scope">
            <!---->
            <el-button
              type="primary"
              icon="el-icon-edit"
              size="mini"
              @click="showEditDialog(scope.row.uID)"
            ></el-button>
            <!---->
            <el-button
              type="danger"
              icon="el-icon-delete"
              size="mini"
              @click="removeUserById(scope.row.uID)"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!---->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.page"
        :page-sizes="[10, 20, 50, 100]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>
    <!---->
    <el-dialog
      title=""
      :visible.sync="addDialogVisible"
      width="50%"
      @close="addDialogClosed"
    >
      <!---->
      <el-form
        :model="addForm"
        :rules="addFormRules"
        ref="addFormRef"
        label-width="70px"
      >
        <el-form-item label="" prop="uRealName">
          <el-input v-model="addForm.uRealName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="uLoginName">
          <el-input v-model="addForm.uLoginName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="uPassWord">
          <el-input v-model="addForm.uPassWord"></el-input>
        </el-form-item>
        <el-form-item label="" prop="uSex">
          <el-radio-group v-model="addForm.uSex">
            <el-radio :label="1"></el-radio>
            <el-radio :label="2"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="" prop="uEmail">
          <el-input v-model="addForm.uEmail"></el-input>
        </el-form-item>
        <el-form-item label="" prop="uPhone">
          <el-input v-model="addForm.uPhone"></el-input>
        </el-form-item>
        <el-form-item label="" prop="uAddress">
          <el-input type="textarea" v-model="addForm.uAddress"></el-input>
        </el-form-item>
      </el-form>
      <!---->
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addUser"> </el-button>
      </span>
    </el-dialog>
    <!---->
    <el-dialog
      title=""
      :visible.sync="editdialogVisible"
      width="50%"
      @close="editDialogClosed"
    >
      <!---->
      <el-form
        :model="editForm"
        :rules="editFormRules"
        ref="editFormRef"
        label-width="70px"
      >
        <el-form-item label="" prop="uRealName">
          <el-input v-model="editForm.uRealName"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="editForm.uLoginName" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="uSex">
          <el-radio-group v-model="editForm.uSex">
            <el-radio :label="1"></el-radio>
            <el-radio :label="2"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="" prop="uEmail">
          <el-input v-model="editForm.uEmail"></el-input>
        </el-form-item>
        <el-form-item label="" prop="uPhone">
          <el-input v-model="editForm.uPhone"></el-input>
        </el-form-item>
        <el-form-item label="" prop="uAddress">
          <el-input type="textarea" v-model="editForm.uAddress"></el-input>
        </el-form-item>
        <el-form-item label="" prop="rIDs">
          <el-select
            v-model="editForm.rIDs"
            multiple
            filterable
            placeholder=""
          >
            <el-option
              v-for="item in roles"
              :key="item.rID"
              :label="item.rName"
              :value="item.rID"
            >
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editdialogVisible = false"> </el-button>
        <el-button type="primary" @click="editUserInfo"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data () {
    // 
    var checkEmail = (rule, value, cb) => {
      // 
      const regEmail = /^[A-Za-z0-9\u4e00-\u9fa5]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/
      if (regEmail.test(value)) {
        // 
        return cb()
      }
      cb(new Error(''))
    }
    // 
    var checkPhone = (rule, value, cb) => {
      // 
      const regPhone = /^1[3456789]\d{9}$/
      if (regPhone.test(value)) {
        // 
        return cb()
      }
      cb(new Error(''))
    }
    return {
      // 
      queryInfo: {
        key: '',
        // 
        page: 1,
        pagesize: 10
      },
      roles: [],
      userlist: [],
      total: 0,
      // 
      addDialogVisible: false,
      // 
      editdialogVisible: false,
      // 
      addForm: {
        uLoginName: '',
        uRealName: '',
        uPassWord: '',
        uSex: 1,
        uEmail: '',
        uPhone: '',
        uAddress: ''
      },

      // 
      addFormRules: {
        uLoginName: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 10, message: ' 3~10 ', trigger: 'blur' }
        ],
        uRealName: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 100, message: ' 3~100 ', trigger: 'blur' }
        ],
        uPassWord: [
          { required: true, message: '', trigger: 'blur' },
          { min: 6, max: 15, message: ' 6~15', trigger: 'blur' }
        ],
        uSex: [{ required: true, message: '', trigger: 'change' }],
        uEmail: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkEmail, trigger: 'blur' }
        ],
        uPhone: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkPhone, trigger: 'blur' }
        ],
        uAddress: [
          { required: true, message: '', trigger: 'blur' },
          { min: 6, max: 100, message: ' 6~100', trigger: 'blur' }
        ]
      },
      // 
      editForm: {},
      // 
      editFormRules: {
        uLoginName: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 10, message: ' 3~10 ', trigger: 'blur' }
        ],
        uRealName: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 100, message: ' 3~100 ', trigger: 'blur' }
        ],
        uSex: [{ required: true, message: '', trigger: 'change' }],
        uEmail: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkEmail, trigger: 'blur' }
        ],
        uPhone: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkPhone, trigger: 'blur' }
        ],
        uAddress: [
          { required: true, message: '', trigger: 'blur' },
          { min: 6, max: 100, message: ' 6~100', trigger: 'blur' }
        ]
      }
    }
  },
  created () {
    this.getUserList()
  },
  methods: {
    async getUserList () {
      const { data: res } = await this.$http.get('user/users', {
        params: this.queryInfo
      })
      // console.log(res);
      if (!res.success) return this.$message.error(res.msg)
      this.userlist = res.response.data
      this.total = res.response.dataCount
    },
    // 
    formatterSex: function (row, column) {
      return row.uSex == 1 ? '' : row.uSex == 2 ? '' : ''
    },
    formatDateTime: function (row, column, cellValue, index) {
      return cellValue.replace('T', ' ')
    },
    // pagesize
    handleSizeChange (newSize) {
      // console.log(newSize);
      this.queryInfo.pagesize = newSize
      this.getUserList()
    },
    // 
    handleCurrentChange (newPage) {
      // console.log(newPage);
      this.queryInfo.page = newPage
      this.getUserList()
    },
    // switch
    async userStateChanged (userInfo) {
      const { data: res } = await this.$http.put(
        `/user/userstate?userid=${userInfo.uID}&status=${userInfo.uIsDelete}`
      )
      if (!res.success) {
        userInfo.uIsDelete = !userInfo.uIsDelete
        return this.$message.error('')
      }
      this.$message.success('')
    },
    // 
    addDialogClosed () {
      this.$refs.addFormRef.resetFields()
    },
    // 
    addUser () {
      this.$refs.addFormRef.validate(async (valid) => {
        // console.log(valid)
        if (!valid) return
        // 
        const para = Object.assign({}, this.addForm)
        const { data: res } = await this.$http.post('user/adduser', para)
        if (!res.success) return this.$message.error(res.msg)
        this.$message.success('')
        // 
        this.addDialogVisible = false
        // 
        this.getUserList()
      })
    },
    // 
    async showEditDialog (id) {
      const { data: res } = await this.$http.get('user/userinfo', {
        params: { uid: id }
      })
      if (!res.success) return this.$message.error(res.msg)
      const { data: re } = await this.$http.get('roles/rolelist')
      if (!re.success) return this.$message.error(re.msg)
      this.roles = re.response
      console.log(re.response)
      this.editForm = res.response
      this.editdialogVisible = true
    },
    // 
    editDialogClosed () {
      this.$refs.editFormRef.resetFields()
    },
    // 
    editUserInfo () {
      this.$refs.editFormRef.validate(async (valid) => {
        console.log(this.editForm)
        if (!valid) return
        // 
        const { data: res } = await this.$http.put(
          'user/updateuser',
          this.editForm
        )
        if (!res.success) return this.$message.error('')
        // 
        this.editdialogVisible = false
        // 
        this.getUserList()
        // 
        this.$message.success('')
      })
    },
    // id
    removeUserById (id) {
      // 
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
        .then(async () => {
          // 
          const { data: res } = await this.$http.delete('user/deleteuser', {
            params: { uid: id }
          })
          if (!res.success) return this.$message.error(res.msg)
          this.$message({
            type: 'success',
            message: '!'
          })
          // 
          this.getUserList()
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
    }
  }
}
</script>
<style lang="less" scoped>
</style>
