<template>
  <div>
    <!---->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!---->
    <el-card>
      <!---->
      <el-row>
        <el-col>
          <el-button type="primary" @click="showAddDialog"></el-button>
        </el-col>
      </el-row>
      <!---->
      <el-table
        :data="menulist"
        border
        stripe
        row-key="mID"
        :tree-props="{ children: 'children', hasChildren: 'hasChildren' }"
      >
        <el-table-column type="index"></el-table-column>
        <el-table-column label="" prop="mName"></el-table-column>
        <el-table-column label="" prop="mPath"></el-table-column>
        <el-table-column label="" prop="mIcon"></el-table-column>
        <el-table-column label="" prop="mDescription"></el-table-column>
        <el-table-column label="" prop="mCreateBy"></el-table-column>
        <el-table-column label="" prop="mCreateTime" :formatter="formatDateTime"></el-table-column>
        <el-table-column label="" prop="mModifyBy"></el-table-column>
        <el-table-column label="" prop="mUpdateTime" :formatter="formatDateTime"></el-table-column>
        <el-table-column label="" width="180">
          <template slot-scope="scope">
            <el-button
              type="primary"
              size="mini"
              icon="el-icon-edit"
              @click="showEditDialog(scope.row.mID)"
              ></el-button
            >
            <el-button
              type="danger"
              size="mini"
              icon="el-icon-delete"
              @click="removeMenuById(scope.row.mID)"
              ></el-button
            >
          </template>
        </el-table-column>
      </el-table>
    </el-card>
    <!---->
    <el-dialog
      title=""
      :visible.sync="addDialogVisible"
      width="50%"
      @close="addDialogClosed"
    >
      <el-form
        ref="addFormRef"
        :model="addForm"
        :rules="addFormRules"
        label-width="80px"
      >
        <el-form-item label="" width="" prop="arrPid">
          <el-cascader
            :options="moptions"
            change-on-select
            :props="{
              expandTrigger: 'hover',
              checkStrictly: true,
            }"
            emitPath="false"
            style="width: 100%"
            clearable
            placeholder=""
            v-model="addForm.arrPid"
          ></el-cascader>
        </el-form-item>
        <el-form-item label="" prop="mName">
          <el-input v-model="addForm.mName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="mPath">
          <el-tooltip
            effect="dark"
            content="-,/roles"
            placement="top"
          >
            <el-input v-model="addForm.mPath"></el-input>
          </el-tooltip>
        </el-form-item>
        <el-form-item label="" prop="mOrderSort">
          <el-input v-model="addForm.mOrderSort"></el-input>
        </el-form-item>
        <el-form-item label="" prop="mIcon">
          <el-tooltip
            effect="dark"
            content="el-icon-menu"
            placement="top"
          >
            <el-input v-model="addForm.mIcon"></el-input>
          </el-tooltip>
        </el-form-item>
        <el-form-item label="" prop="mDescription">
          <el-input v-model="addForm.mDescription"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addMenu"> </el-button>
      </span>
    </el-dialog>
    <!---->
    <el-dialog title="" :visible.sync="editDialogVisible" width="50%">
      <el-form
        ref="editFormRef"
        :model="editForm"
        :rules="editFormRules"
        label-width="80px"
        @close="editDialogClosed"
      >
        <el-form-item label="" width="" prop="arrPid">
          <el-cascader
            :options="moptions"
            change-on-select
            emitPath="false"
            style="width: 400px"
            clearable
            placeholder=""
            v-model="editForm.arrPid"
          ></el-cascader>
        </el-form-item>
        <el-form-item label="" prop="mName">
          <el-input v-model="editForm.mName" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="mPath">
          <el-tooltip
            effect="dark"
            content="-,/roles"
            placement="top"
          >
            <el-input v-model="editForm.mPath"></el-input>
          </el-tooltip>
        </el-form-item>
        <el-form-item label="" prop="mOrderSort">
          <el-input v-model="editForm.mOrderSort"></el-input>
        </el-form-item>
        <el-form-item label="" prop="mIcon">
          <el-tooltip
            effect="dark"
            content="el-icon-menu"
            placement="top"
          >
            <el-input v-model="editForm.mIcon"></el-input>
          </el-tooltip>
        </el-form-item>
        <el-form-item label="" prop="mDescription">
          <el-input v-model="editForm.mDescription"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editMenuInfo"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data () {
    return {
      // 
      menulist: [],
      // 
      addDialogVisible: false,
      // 
      addForm: {
        arrPid: []
      },
      // 
      moptions: [],
      // 
      addFormRules: {
        mPid: [{ required: true, message: '', trigger: 'blur' }],
        mName: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 10, message: ' 3~10 ', trigger: 'blur' }
        ],
        mPath: [{ required: true, message: '', trigger: 'blur' }],
        mOrderSort: [
          { required: true, message: '', trigger: 'blur' }
        ],
        mIcon: [{ required: true, message: '', trigger: 'blur' }],
        mDescription: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 100, message: ' 3~100 ', trigger: 'blur' }
        ]
      },
      // 
      editDialogVisible: false,
      // 
      editForm: {},
      // 
      editFormRules: {
        mPid: [{ required: true, message: '', trigger: 'blur' }],
        mPath: [{ required: true, message: '', trigger: 'blur' }],
        mOrderSort: [
          { required: true, message: '', trigger: 'blur' }
        ],
        mIcon: [{ required: true, message: '', trigger: 'blur' }],
        mDescription: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 100, message: ' 3~100 ', trigger: 'blur' }
        ]
      }
    }
  },
  created () {
    this.getMenuList()
  },
  methods: {
    // 
    async getMenuList () {
      const { data: res } = await this.$http.get('menus/list')
      if (!res.success) return this.$message.error(res.msg)
      this.menulist = res.response
    },
    // 
    formatDateTime: function (row, column, cellValue, index) {
      return cellValue.replace('T', ' ')
    },
    // 
    addDialogClosed () {
      this.$refs.addFormRef.resetFields()
    },
    // 
    async showAddDialog () {
      this.addDialogVisible = true
      const { data: res } = await this.$http.get('menus/rootlist')
      if (!res.success) return this.$message.error(res.msg)
      this.moptions = res.response.children
    },
    // 
    addMenu () {
      this.$refs.addFormRef.validate(async (valid) => {
        if (!valid) return
        this.addForm.mPid = this.addForm.arrPid.pop()
        // intstring
        this.addForm.mOrderSort = parseInt(this.addForm.mOrderSort)
        const { data: res } = await this.$http.post(
          'menus/addmenu',
          this.addForm
        )
        if (!res.success) return this.$message.error(res.msg)
        this.$message.success('')
        // 
        this.addDialogVisible = false
        // 
        this.getMenuList()
      })
    },
    // 
    async showEditDialog (id) {
      // id
      const { data: res } = await this.$http.get('menus/menu', {
        params: { mid: id }
      })
      if (!res.success) return this.$message.error(res.msg)
      this.editForm = res.response
      console.log(this.editForm)
      // 
      const { data: re } = await this.$http.get('menus/rootlist')
      if (!re.success) return this.$message.error(re.msg)
      this.moptions = re.response.children

      this.editDialogVisible = true
    },
    // 
    editDialogClosed () {
      this.$refs.editFormRef.resetFields()
    },
    // 
    editMenuInfo () {
      this.$refs.editFormRef.validate(async (valid) => {
        if (!valid) return
        this.editForm.mPid = this.editForm.arrPid.pop()
        const { data: res } = await this.$http.put(
          'menus/updatemenu',
          this.editForm
        )
        if (!res.success) return this.$message.error(res.msg)
        this.editDialogVisible = false
        // 
        this.getMenuList()
        this.$message.success('!')
      })
    },
    // 
    removeMenuById (id) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(async () => {
        const { data: res } = await this.$http.delete('menus/delete', { params: { id: id } })
        if (!res.success) return this.$message.error(res.msg)
        this.$message({
          type: 'success',
          message: '!'
        })
        // 
        this.getMenuList()
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        })
      })
    }
  }
}
</script>
<style lang="less" scoped>
</style>
