<template>
  <div>
    <!---->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!---->
    <el-card>
         <!---->
      <el-row :gutter="20">
        <el-col :span="8">
          <el-input
            placeholder=""
            v-model="queryInfo.key"
            clearable
            @clear="getInterfaceList"
          >
            <el-button
              @click="getInterfaceList"
              slot="append"
              icon="el-icon-search"
            ></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="addDialogVisible = true"
            ></el-button
          >
        </el-col>
      </el-row>
      <el-table :data="interfaceList" border stripe>
        <el-table-column type="index"></el-table-column>
        <el-table-column label="" prop="iLinkUrl"></el-table-column>
        <el-table-column label="" prop="iDescription"></el-table-column>
        <el-table-column label="" prop="iCreateBy"></el-table-column>
        <el-table-column label="" prop="iCreateTime"></el-table-column>
        <el-table-column label="" prop="iEnabled">
            <template slot-scope="scope">
                <el-tag type="success" v-if="scope.row.iEnabled==true"></el-tag>
                <el-tag type="danger" v-else></el-tag>
            </template>
        </el-table-column>
      </el-table>
    </el-card>
  </div>
</template>
<script>
export default {
  data () {
    return {
      // 
      queryInfo: {
        key: '',
        // 
        page: 1,
        pagesize: 10
      },
      // 
      interfaceList: []
    }
  },
  created () {
    // 
    this.getInterfaceList()
  },
  methods: {
    // 
    async getInterfaceList () {
      const { data: res } = await this.$http.get('interfaces/get', {
        params: this.queryInfo
      })
      if (!res.success) return this.$message.error(res.msg)
      this.interfaceList = res.response.data
      console.log(res)
    }
  }
}
</script>
<style lang="less" scoped>
</style>
