<template>
  <div>
    <!---->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!---->
    <el-card class="box-card">
      <!---->
      <el-row>
        <el-col>
          <el-button type="primary" @click="addDialogVisible = true"
            ></el-button
          >
        </el-col>
      </el-row>
      <!---->
      <el-table :data="rolelist" border stripe>
        <!---->
        <el-table-column type="expand">
          <template slot-scope="scope">
            <el-row
              v-for="item1 in scope.row.children"
              :key="item1.mID"
              :class="['bdbottom', 'vcenter']"
            >
              <!---->
              <el-col :span="5">
                <el-tag
                  @close="removeRightById(scope.row, item1.mID)"
                  closable
                  >{{ item1.mName}}</el-tag
                ><i class="el-icon-caret-right"></i>
              </el-col>
              <!---->
              <el-col :span="19">
                <!--for-->
                <el-row
                  v-for="item2 in item1.children"
                  :key="item2.mName"
                  :class="['bdtop', 'vcenter']"
                >
                  <el-col :span="6">
                    <el-tag
                      @close="removeRightById(scope.row, item2.mID)"
                      closable
                      type="success"
                      >{{ item2.mName }}</el-tag
                    ><i class="el-icon-caret-right"></i>
                  </el-col>
                  <el-col :span="18">
                    <!---->
                    <el-tag
                      @close="removeRightById(scope.row, item3.mID)"
                      closable
                      type="warning"
                      v-for="item3 in item2.children"
                      :key="item3.mID"
                      >{{ item3.mName }}</el-tag
                    >
                  </el-col>
                </el-row>
              </el-col>
            </el-row>
          </template>
        </el-table-column>
        <!---->
        <el-table-column type="index"></el-table-column>
        <el-table-column label="" prop="rName"></el-table-column>
        <el-table-column label="" prop="rDescription"></el-table-column>
        <el-table-column
          label=""
          prop="rCreateTime"
          :formatter="formatDateTime"
        ></el-table-column>
        <el-table-column label="" prop="rCreateBy"></el-table-column>
        <el-table-column
          label=""
          prop="rUpdateTime"
          :formatter="formatDateTime"
        ></el-table-column>
        <el-table-column label="" prop="rModifyBy"></el-table-column>
        <el-table-column label="" width="300">
          <template slot-scope="scope">
            <el-button
              type="primary"
              size="mini"
              icon="el-icon-edit"
              @click="showEditDialog(scope.row.rID)"
              ></el-button
            >
            <el-button
              type="danger"
              size="mini"
              icon="el-icon-delete"
              @click="removeRoleById(scope.row.rID)"
              ></el-button
            >
            <el-button
              type="warning"
              size="mini"
              icon="el-icon-setting"
              @click="showSetPermissionDialog(scope.row)"
              ></el-button
            >
          </template>
        </el-table-column>
      </el-table>
    </el-card>
    <!---->
    <el-dialog
      title=""
      :visible.sync="addDialogVisible"
      width="50%"
      @close="addDialogClosed"
    >
      <el-form
        ref="addFormRef"
        :rules="addFormRules"
        :model="addForm"
        label-width="80px"
      >
        <el-form-item label="" prop="rName">
          <el-input v-model="addForm.rName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="rDescription">
          <el-input v-model="addForm.rDescription"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addRole"> </el-button>
      </span>
    </el-dialog>
    <!---->
    <el-dialog
      title=""
      :visible.sync="editdialogVisible"
      width="50%"
      @close="editDialogClosed"
    >
      <el-form
        ref="editFormRef"
        :model="editForm"
        :rules="editFormRules"
        label-width="80px"
      >
        <el-form-item label="">
          <el-input v-model="editForm.rName" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="rDescription">
          <el-input v-model="editForm.rDescription"></el-input>
        </el-form-item>
        <el-form-item label="" prop="rIsDelete">
          <el-switch v-model="editForm.rIsDelete"></el-switch>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editdialogVisible = false"> </el-button>
        <el-button type="primary" @click="editRoleInfo"> </el-button>
      </span>
    </el-dialog>

    <!---->
    <el-dialog
      title=""
      :visible.sync="setPermissionDialogVisible"
      width="50%"
      @close="setPermissionDialogClosed"
    >
      <!---->
      <el-tree
        :data="rolemenulist"
        :props="treeProps"
        show-checkbox
        node-key="id"
        default-expand-all
        :default-checked-keys="defKeys"
        ref="treeRef"
      ></el-tree>
      <span slot="footer" class="dialog-footer">
        <el-button @click="setPermissionDialogVisible = false"> </el-button>
        <el-button type="primary" @click="setRoleMenu"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data () {
    return {
      // 
      addDialogVisible: false,
      // 
      addForm: {
        rName: '',
        rDescription: ''
      },
      // 
      addFormRules: {
        rName: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 100, message: ' 3~100 ', trigger: 'blur' }
        ],
        rDescription: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 100, message: ' 3~100 ', trigger: 'blur' }
        ]
      },
      // 
      editdialogVisible: false,
      // 
      editForm: {},
      // 
      editFormRules: {
        rDescription: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 100, message: ' 3~100 ', trigger: 'blur' }
        ],
        rIsDelete: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      // 
      rolelist: [],
      // 
      setPermissionDialogVisible: false,
      // 
      rolemenulist: [],
      // 
      treeProps: {
        label: 'name',
        children: 'children'
      },
      // Id
      defKeys: [],
      // id
      rolemenuinfo: {
        rID: 0,
        mIDs: ''
      }
    }
  },
  created () {
    this.getRoleList()
  },
  methods: {
    // 
    addRole () {
      this.$refs.addFormRef.validate(async (valid) => {
        // console.log(valid);
        if (!valid) return
        const { data: res } = await this.$http.post(
          'roles/addrole',
          this.addForm
        )
        if (!res.success) return this.$message.error(res.msg)
        this.$message.success('')
        // 
        this.addDialogVisible = false
        // 
        this.getRoleList()
      })
    },
    // 
    addDialogClosed () {
      this.$refs.addFormRef.resetFields()
    },
    // 
    async showEditDialog (id) {
      const { data: res } = await this.$http.get('roles/getrole', {
        params: { rid: id }
      })
      if (!res.success) return this.$message.error(res.msg)
      this.editForm = res.response
      this.editdialogVisible = true
    },
    // 
    editRoleInfo () {
      this.$refs.editFormRef.validate(async (valid) => {
        if (!valid) return
        const { data: res } = await this.$http.put(
          'roles/updaterole',
          this.editForm
        )
        if (!res.success) return this.$message.error(res.msg)
        // 
        this.editdialogVisible = false
        // 
        this.getRoleList()
        // 
        this.$message.success('')
      })
    },
    // 
    editDialogClosed () {
      this.$refs.editFormRef.resetFields()
    },
    // 
    removeRoleById (id) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
        .then(async () => {
          const { data: res } = await this.$http.delete('roles/delete', {
            params: { rid: id }
          })
          if (!res.success) return this.$message.error(res.msg)
          this.$message({
            type: 'success',
            message: '!'
          })
          // 
          this.getRoleList()
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
    },
    // 
    async getRoleList () {
      const { data: res } = await this.$http.get('roles/rolelist')
      if (!res.success) return this.$message.error(res.msg)
      this.rolelist = res.response
    },
    // id
    removeRightById (role, id) {
      // 
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
        .then(async () => {
          const { data: res } = await this.$http.delete('roles/deletermp', {
            params: { rid: role.rID, mid: id }
          })
          if (!res.success) return this.$message.error(res.msg)
          this.$message({
            type: 'success',
            message: '!'
          })
          role.children = res.response[0].children
          // console.log(role);
          console.log(res.response[0].children)
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
    },
    // 
    formatDateTime: function (row, column, cellValue, index) {
      return cellValue.replace('T', ' ')
    },
    // 
    async showSetPermissionDialog (role) {
      this.rolemenuinfo.rID = role.rID
      // 
      const { data: res } = await this.$http.get('roles/tree')
      if (!res.success) return this.$message.error(res.msg)
      this.rolemenulist = res.response
      // id
      this.getLeafKeys(role, this.defKeys)
      this.setPermissionDialogVisible = true
      console.log(role)
    },
    // iddefKeys
    getLeafKeys (node, arr) {
      // nodechildren
      if (!node.children) {
        return arr.push(node.mID)
      }
      node.children.forEach((item) => {
        this.getLeafKeys(item, arr)
      })
    },
    // 
    setPermissionDialogClosed () {
      this.defKeys = []
    },
    // 
    async setRoleMenu () {
      const keys = [
        ...this.$refs.treeRef.getCheckedKeys(),
        ...this.$refs.treeRef.getHalfCheckedKeys()
      ]
      this.rolemenuinfo.mIDs = keys.join(',')
      const { data: res } = await this.$http.post('roles/setrolemenu', this.rolemenuinfo)
      if (!res.success) return this.$message.error(res.msg)
      this.$message.success('')
      // 
      this.getRoleList()
      // 
      this.setPermissionDialogVisible = false
    }
  }
}
</script>
<style lang="less" scoped>
.el-tag {
  margin: 7px;
}
.bdtop {
  border-top: 1px solid #eee;
}
.bdbottom {
  border-bottom: 1px solid #eee;
}
.vcenter {
  display: flex;
  align-items: center;
}
</style>
